body {
  margin: 0;
  padding: 0;
  font-family: 'Work Sans', 'Source Sans Pro', 'Roboto', 'Oxygen', 'Ubuntu', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
  font-weight: 100;
  overflow: hidden;
}

#root {
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

button:focus {outline:0;}

.Clock {
  text-align: center;
  width: 80vmin;
  height: 80vmin;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  flex-flow: row wrap;
  background: rgba(0,0,0, 0.6);
  border-radius: 100%;
  z-index: 20;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.12),0 1px 5px 0 rgba(0,0,0,.2);
}

.Clock .Times {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  max-width: 100%;
  flex: 0 0 100%;
  align-self: flex-end;
}

.Clock .Hours {
  color: white;
  font-size: 25vmin;
  letter-spacing: 2px;
  text-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.12),0 1px 5px 0 rgba(0,0,0,.2);
}

.Clock .Mins {
  color: white;
  font-size: 10vmin;
  letter-spacing: 1px;
  padding-bottom: 8vmin;
  padding-top: 2vmin;
  box-sizing: border-box;
  width: 8vmin;
  text-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.12),0 1px 5px 0 rgba(0,0,0,.2);
}

.Clock .Secs {
  color: white;
  font-size: 5vmin;
  letter-spacing: 1px;
  margin-left: -7vmin;
  padding-top: 8vmin;
  box-sizing: border-box;
  width: 6vmin;
  text-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.12),0 1px 5px 0 rgba(0,0,0,.2);
}

.Clock .Date {
  color: white;
  font-size: 6vmin;
  display: block;
  width: 100%;
  max-width: 100%;
  flex: 0 0 100%;
  align-self: flex-start;
  word-spacing: 4px;
  text-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.12),0 1px 5px 0 rgba(0,0,0,.2);
}

.Clock .Minutes {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 2px solid white;
  border-radius: 100%;
  display: block;
  position: absolute;
}

.Clock .Minutes-Negative {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 2px dotted white;
  border-radius: 100%;
  display: block;
  position: absolute;
}

.Clock .Seconds {
  position: absolute;
  height: 50%;
  top: 0;
}
.Video-Overlay {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
  z-index: 5;
}

.Video-Container {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (min-aspect-ratio: 16/9) {
  .Video-Background {
    height: 300%;
    top: -100%;
  }
}
@media (max-aspect-ratio: 16/9) {
  .Video-Background {
    width: 300%;
    left: -100%;
  }
}
@supports (object-fit: cover) {
  .Video-Background video {
    object-fit: cover;
  }
}

.Video-Previous-Button, .Video-Next-Button {
  position: fixed;
  z-index: 8;
  border: 0;
  background: transparent;
  top: calc(50vh - 12px);
  cursor: pointer;
}

.Video-Previous-Button svg, .Video-Next-Button svg {
  -webkit-filter: drop-shadow(0 2px 2px rgba(0,0,0,.14)) drop-shadow(0 3px 1px rgba(0,0,0,.12)) drop-shadow(0 1px 5px rgba(0,0,0,.2));
          filter: drop-shadow(0 2px 2px rgba(0,0,0,.14)) drop-shadow(0 3px 1px rgba(0,0,0,.12)) drop-shadow(0 1px 5px rgba(0,0,0,.2));
}

.Video-Previous-Button {
  left: 0;
}

.Video-Next-Button {
  right: 0;
}

@media screen and (max-width: 340px) {
  .Video-Previous-Button, .Video-Next-Button {
    display: none;
  }
}

@media screen and (min-width: 400px) and (orientation: landscape) {
  .Video-Previous-Button {
    left: 3vmin;
  }
  .Video-Next-Button {
    right: 3vmin
  }
}

.Github-Link {
  position: fixed;
  bottom: 2px;
  left: calc(50% - 10px);
  -webkit-filter: drop-shadow(0 2px 2px rgba(0,0,0,.14)) drop-shadow(0 3px 1px rgba(0,0,0,.12)) drop-shadow(0 1px 5px rgba(0,0,0,.2));
          filter: drop-shadow(0 2px 2px rgba(0,0,0,.14)) drop-shadow(0 3px 1px rgba(0,0,0,.12)) drop-shadow(0 1px 5px rgba(0,0,0,.2));
  z-index: 8;
}
